import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Product } from 'src/app/models/products';
import { CartSend } from 'src/app/models/cartSend';
import { ProductSend } from 'src/app/models/sendProduct';
import { BillsService } from 'src/app/services/bills.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-korpa-radnik',
  templateUrl: './korpa-radnik.component.html',
  styleUrls: ['./korpa-radnik.component.css']
})
export class KorpaRadnikComponent implements OnInit {
  korpa = []
  constructor(private http: HttpClient,
    private billsService: BillsService,
    private router: Router) { }
  endpointURL: string = 'http://78.46.117.245:9000/';
  endpointURL1: string = 'http://78.46.117.245:9000/logout';
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});
  product2 : Product[] 
  info : ProductSend [] = []
  cartSend : CartSend [] = []
  response : any
  sumPrice : string

  checkingQuantity : boolean = true;
  newCart: any
  pozicija : string = localStorage.getItem('username');

  ngOnInit() {
    this.billsService.getAllBills();
    this.korpa = JSON.parse(localStorage.getItem("cart"))
    this.http.get<Product[]>(this.endpointURL + 'products/' ,  {headers : this.headers}  )
      .subscribe(result => { this.product2 = result;
        let l = this.product2.length
        for(let idk of this.korpa){
          for(var i=0;i< l;i++){
                if(idk == this.product2[i].id){
                  let current = {
                    id2: '' + i + '',
                    id: this.product2[i].id,
                    price: this.product2[i].price,
                    quantity: this.product2[i].quantity,
                    name: this.product2[i].name
                  }
                  
                  this.info.push(current);
                  }
              }
          }
          //localStorage.setItem("currentCart", JSON.stringify(this.info));
});

  }

  logout(){
    this.http.post(this.endpointURL1,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
      }
    })
  }
  
  deleteCartProduct(event){
    const id = event.srcElement.id;
    this.info.splice (id,1)
    this.korpa.splice(id,1)
    localStorage.setItem("cart", JSON.stringify(this.korpa))
    window.alert("Proizvod je uklonjen iz korpe!")

  }
  checkOut(event){
    event.preventDefault()
    const target = event.target
    let l = this.info.length
    for(var i = 0; i< l;i++){
      var id = this.info[i].id;
      var id2= "qprice"+id;
      let quantity = document.getElementsByTagName('input')[i].value;
      //var q = quantity.
     if( quantity<=this.info[i].quantity){
      let current = {
        productID: id,
      quantity: quantity

      }
      this.cartSend.push(current)
            
    }
    else{
      this.checkingQuantity=false;
    }
    }
    //localStorage.setItem('cartSend', JSON.stringify(this.cartSend));
    if(this.checkingQuantity){
    this.http.post(this.endpointURL + 'buy', this.cartSend, {headers: this.headers})
    .subscribe(response => 
      {
        this.response=response,
        this.sumPrice = this.response.price
        window.alert("Uspena kupovina!")
        localStorage.removeItem('cart');
      },(err) => {this.handleError(err)}) }
      else {
        this.cartSend = []
        this.checkingQuantity = true;
        window.alert("Uneli ste veci broj proizvoda nego sto je dostupno!")
      }
  }
  
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }


}