import { Component, OnInit } from '@angular/core';
import { InvoicesService } from 'src/app/services/invoices.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-prikaz-komercijalista',
  templateUrl: './prikaz-komercijalista.component.html',
  styleUrls: ['./prikaz-komercijalista.component.css']
})
export class PrikazKomercijalistaComponent implements OnInit {

  constructor(private invoiceService: InvoicesService,
              private http: HttpClient,
              private router: Router) { }
  
  pozicija : string = localStorage.getItem('username');
  endpointURL: string = 'http://78.46.117.245:9000/logout';
  headers = new HttpHeaders({
                    
    'Content-Type' : 'application/json;charset=UTF-8',
    'token' : localStorage.getItem("token")
    });

  ngOnInit() {
    this.invoiceService.getAllInvoices();
  }
  openPDF(event){
    event.preventDefault();
    const id = event.srcElement.id;
    this.invoiceService.getPDF(id);
  }

  logout(){
    this.http.post(this.endpointURL,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {
      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }

}
