  ngOnInit() {
    this.producersService.getAllProducers();
    this.productTypeService.getAllProductTypes();
    this.billsService.getAllBills();
    this.uploadForm = this.formBuilder.group({
      image: [''], 
      name : [''],
      price: [''],
      producer: [''], 
      productType : [''],
      productURL : [''],
      properties : [''],
      quantity : [''],
      warranty : [''],
      vendorPrice : ['']
      });
    this.http.get<OrderResponse[]>(this.endpointURL, {headers: this.headers}).subscribe(
      result => {
          this.inactiveOrders = result;
      }
    )

    this.komercijalista.getSuppliers().subscribe(result => {this.dobavljaci = result})

  }

  logout(){
    this.http.post(this.endpointURL5,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }
  
