import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ProductsService } from 'src/app/services/products.service';
import { Product } from 'src/app/models/products';
import { OrderResponse } from 'src/app/models/orderResponse';
import { ConvertActionBindingResult } from '@angular/compiler/src/compiler_util/expression_converter';
import { KomercijalistaComponent } from '../komercijalista.component';
import { Suppliers } from 'src/app/models/suppliers';
import { Router } from '@angular/router';

@Component({
  selector: 'app-narucivanje-komercijalista',
  templateUrl: './narucivanje-komercijalista.component.html',
  styleUrls: ['./narucivanje-komercijalista.component.css'],
  providers: [KomercijalistaComponent]
})
export class NarucivanjeKomercijalistaComponent implements OnInit {

  constructor(private http: HttpClient,
    private productService: ProductsService,
    private komercijalista: KomercijalistaComponent,
    private router: Router) { }

  endpointURL: string = 'http://78.46.117.245:9000/product_orders/?status=inactive';
  endpointURL2: string = 'http://78.46.117.245:9000/orders/';
  endpointURL3: string = 'http://78.46.117.245:9000/logout';

  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});

  pozicija : string = localStorage.getItem('username');

  inactiveOrders : OrderResponse[] = [];

  dobavljaci : Suppliers[];


  

  ngOnInit() {
    this.http.get<OrderResponse[]>(this.endpointURL, {headers: this.headers}).subscribe(
      result => {
          this.inactiveOrders = result;
      }
    )

    this.komercijalista.getSuppliers().subscribe(result => {this.dobavljaci = result})

    
  }

  logout(){
    this.http.post(this.endpointURL3,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }

  orders = [];

  orderProductManager(event){
    event.preventDefault();

    var id = event.srcElement.id;

    

    const id2 = parseInt(id)
    
    const quantity = document.getElementsByTagName("input")[id2].value
    const orderID = this.inactiveOrders[id].orderID;
    let current = {
      "orderID": orderID,
      "quantity": quantity
    }
    this.orders.push(current);
    this.inactiveOrders.splice(id,1);


    //const value = document.getElementById("input_"+id).getElementsByTagName("input")[0].value;

    
    
  }
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }
  sendOrder(event){
    event.preventDefault();

    const target = event.target;

    var supplierID = target.querySelector("#supplierID").value;
    
    this.http.post(this.endpointURL2, 
    {
        "orders": this.orders,
        "supplierID": supplierID
    },
    {
      headers: this.headers
    }).subscribe(response => {
      window.alert("Uspesno narucen proizvod!");
    },(err) => {this.handleError(err)})

  }

}
