import { Component, OnInit } from '@angular/core';
import {  HttpClient, HttpHeaders } from '@angular/common/http';
import { KomercijalistaComponent } from '../komercijalista.component';
import { ProductsService } from 'src/app/services/products.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-naruceno-komercijalista',
  templateUrl: './naruceno-komercijalista.component.html',
  styleUrls: ['./naruceno-komercijalista.component.css']
})
export class NarucenoKomercijalistaComponent implements OnInit {

  constructor(private http: HttpClient,
    private productService: ProductsService,
    private router: Router) { }

  endpointURL: string = 'http://78.46.117.245:9000/orders/?arrived=true';
  endpointURL2: string = 'http://78.46.117.245:9000/orders/?arrived=false';
  endpointURL3: string = 'http://78.46.117.245:9000/orders/';
  endpointURL4: string = 'http://78.46.117.245:9000/logout';

  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});

  pozicija : string = localStorage.getItem('username');
  trueArrived = []
  falseArrived = []
  ngOnInit() {
    this.http.get<any>(this.endpointURL,{
      headers:this.headers
    }).subscribe(result=>{
      this.trueArrived = result
    })
    this.http.get<any>(this.endpointURL2,{
      headers:this.headers
    }).subscribe(result=>{
      this.falseArrived = result
    })
  }

  logout(){
    this.http.post(this.endpointURL4,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }
  confirmOrder(event, productID){
      event.preventDefault()
      const id = productID;
      const newEndPointURL= this.endpointURL3 + id + "/invoice"

      const index = event.srcElement.id

      this.http.get(newEndPointURL, {
        headers:this.headers
      }).subscribe(response=>{
      },(err) => {this.handleError(err)})

      var that = this;
    setTimeout(function(){that.http.get<any>(that.endpointURL,{
      headers:that.headers
    }).subscribe(result=>{
      that.trueArrived = result
    })}, 250) 


    this.falseArrived.splice(index,1);



  }
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }

}
