import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ExchangeProductService {

  constructor(private http: HttpClient) { }
  endpointURL: string = 'http://78.46.117.245:9000/';
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});


 returnProduct(billID,givinProductID,takingProductID ): Observable<any>{
    return this.http.post(this.endpointURL + 'soldproducts/substitute/',{
        'billId': billID,
        'oldProduct': givinProductID,
        'newProduct': takingProductID
    },
    {
      headers: this.headers
    })
  }
}
