import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SingleServiceService {

  constructor(private http: HttpClient) { }

  endpointURL: string = 'http://78.46.117.245:9000/products/';

  headers = new HttpHeaders({'Content-Type' : 'application/json;charset=UTF-8',
                             'token' : localStorage.getItem("token")}); 

  singleStatistics : any;     
  
  //Ovo je glavna metoda getSingleStatistics koja uzima id iz admin komponente
  //ali sam istu iskopirao u single-stat komponentu i odmah tamo uzimam response za id 
  //ali svakako neka ostane servis, ako ga ne pozovemo nece se ni koristiti svakako
  
  getSingleStatistics(id){
    return this.http.get(this.endpointURL + id + "/statistics", {headers: this.headers}).subscribe(
      result => {
          this.singleStatistics = result;
      }
    )
  }
}
