  recoverFromList(){
    for(let i = 0; i<this.recoverList.length; i++){
      this.automaticProducts.push(this.recoverList[i])
    }
    this.approveList = [];
    this.fleg = true;
    (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = false;
    this.selectVariable = false;
    
    if(this.fleg){
      this.recoverList = []
      this.fleg = false;
    }
  }

