import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Product } from '../models/products';

@Injectable({
  providedIn: 'root'
})
export class OrderService {

  constructor(private http: HttpClient) { }
  endpointURL: string = 'http://78.46.117.245:9000/products/';
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});


  products: Product[];
  zeroProducts: Product[] =[];

  getZeroProducts(){
    return this.http
    .get<Product[]>(this.endpointURL,  {headers : this.headers}  )
    .subscribe(result => {
    this.products = result;
    this.zeroProducts = [];
    for(var i = 0; i<this.products.length; i++){
      if(this.products[i].quantity == "0"){
          this.zeroProducts.push(this.products[i]);
      }
    }

    localStorage.setItem('zeroProducts', JSON.stringify(this.zeroProducts));
    });
    }
}
