import { Component, OnInit } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Product } from 'src/app/models/products';

@Component({
  selector: 'app-automatski-popust-komercijalista',
  templateUrl: './automatski-popust-komercijalista.component.html',
  styleUrls: ['./automatski-popust-komercijalista.component.css']
})
export class AutomatskiPopustKomercijalistaComponent implements OnInit {

  constructor(private http: HttpClient,
              private router: Router) { }

  endpointURL_approve: string = 'http://78.46.117.245:9000/products/discount/approve';
  endpointURL: string = 'http://78.46.117.245:9000/products/discount/';
  endpointURL3: string = 'http://78.46.117.245:9000/logout';

  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});
  automaticProducts : Product[] = []

  pozicija : string = localStorage.getItem('username');

  

  ngOnInit() {

    this.getAutomaticProducts().subscribe(response => {
      this.automaticProducts = response;
    })

    

    var that = this;
    setTimeout(function(){ if(that.automaticProducts.length <= 0){
      document.getElementById("omoguciButton").setAttribute("disabled", "true");
      (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = true;
    }}, 1000)
   
  }

  logout(){
    this.http.post(this.endpointURL3,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }

  selectedProducts = [];
  selectVariable : boolean = false;
  recoverList = [];

  selectApproveAll(event){

    
    
    if(this.selectVariable){
      this.selectVariable = false;

    }
    else{
      this.selectVariable = true;
      (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = true;
    }

    for(let i=0; i<this.automaticProducts.length; i++){
      this.selectedProducts.push(this.automaticProducts[i].id);
      this.recoverList.push(this.automaticProducts[i])
      var allCurrent = {
        "id" : this.automaticProducts[i].id,
        "selectedProductName" : this.automaticProducts[i].name
      }
      this.approveList.push(allCurrent);
    }

    (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = true;
    //document.getElementById("allButtonTD").innerHTML = "";
    
    this.automaticProducts = [];

    /* if(this.selectVariable){
        for(let i=0; i<this.automaticProducts.length; i++){
          document.getElementsByClassName("selectButton")[i].setAttribute("disabled","true")
        }
    }
    else{
      for(let i=0; i<this.automaticProducts.length; i++){
        document.getElementsByClassName("selectButton")[i].removeAttribute("disabled");
      }
    } */
  }

  fleg : boolean = false;

  recoverFromList(){
    for(let i = 0; i<this.recoverList.length; i++){
      this.automaticProducts.push(this.recoverList[i])
    }
    this.approveList = [];
    this.fleg = true;
    (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = false;
    this.selectVariable = false;
    
    if(this.fleg){
      this.recoverList = []
      this.fleg = false;
    }
  }

  approveSelectedOrAll(){

    this.http.post(this.endpointURL_approve, 
     {"products": this.selectedProducts,
      "all": this.selectVariable
     },
     {headers: this.headers}).subscribe(response => {
       window.alert("Automatski popust je omoguen!")
       this.approveList = []
     },(err) => {this.handleError(err)})
     
     if(this.automaticProducts.length <= 0){
       document.getElementById("omoguciButton").setAttribute("disabled", "true")
     }
 }

  approveList = [];

  addToApprove(event, productName, index){
    event.preventDefault();
    const id = event.srcElement.id;
    const selectedProductName = productName;
    const productIndex = index;
    this.recoverList.push(this.automaticProducts[productIndex])
    this.automaticProducts.splice(productIndex,1)
    if(this.automaticProducts.length <= 0){
      this.selectVariable = false;
      (<HTMLInputElement> document.getElementById("selectAllButton")).disabled = true;
    } 
    this.selectedProducts.push(id.toString());

    var current = {
      "id": id,
      "selectedProductName": selectedProductName
    }

    this.approveList.push(current)



    /* console.log("ID selektovanog proizvoda je: ", id,
                "Ime proizvoda je: " + selectedProductName,
                "Lista selektovanih proizvoda je: ", this.selectedProducts,
                "Index selektovanog proizvoda je: ", productIndex,
                "Lista approve proizvoda je: ", this.approveList) */
  }

  getAutomaticProducts(){
    return this.http.get<Product[]>(this.endpointURL, {headers: this.headers})
  }

  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }

}
