import { Component, OnInit } from '@angular/core';
import { OrderService } from '../services/order.service';
import { BillsService } from '../services/bills.service';
import { ProductsService } from '../services/products.service';
import { ExchangeProductService } from '../services/exchange-product.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-radnik',
  templateUrl: './radnik.component.html',
  styleUrls: ['./radnik.component.css']
})
export class RadnikComponent implements OnInit {

  constructor(private router: Router,private orderService: OrderService,
    private http: HttpClient,
    private billsService: BillsService,
              private productService: ProductsService,
              private exchangeProductService: ExchangeProductService) { }

  pozicija : string = localStorage.getItem('username');



  endpointURL: string = 'http://78.46.117.245:9000/logout'
  endpointURL3: string = 'http://78.46.117.245:9000/products/';

              headers = new HttpHeaders({
                    
                    'Content-Type' : 'application/json;charset=UTF-8',
                    'token' : localStorage.getItem("token")
                    });

  idProduct: any
  korpa = JSON.parse(localStorage.getItem("cart"));


  ngOnInit() {

    this.billsService.getAllBills();
    this.productService.getAllProducts();
    this.orderService.getZeroProducts();
  }

  logout(){
    this.http.post(this.endpointURL,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
      }
    })
  }

  myDataSource: any;
  gotData = false;
  dataSource: any;
  
  singleStatistic(id){
    /* event.preventDefault();
    const id = event.srcElement.id;
    const newEndpointURL = this.endpointURL2 + id + "/statistics";

    this.http.get(newEndpointURL, {headers: this.headers}).subscribe(result => {
      this.dataSource = result;
      localStorage.setItem("singleStat", JSON.stringify(result));

      //console.log(result, this.dataSource) 
      //console.log(result)

    }) */
     return this.http.get(this.endpointURL3 + id + "/statistics", {headers: this.headers}).subscribe(
      result => {
          this.myDataSource = result
          this.dataSource = {
            chart: {
              "caption": "Statistika",
              "subCaption": "Proizvoda",
              "xAxisName": "Datum",
              "yAxisName": "Broj prodanih proizvoda",
              "theme": "fusion",
          },
          "data": this.myDataSource
          };
      }
    ) 
  }

  exchangeProduct(event){
    event.preventDefault()
    const target = event.target
    const billID = target.querySelector('#givingID').value;
    const givinProductID = target.querySelector('#givinProductID').value;
    const takingProductID = target.querySelector('#takingProductID').value;

    this.exchangeProductService.returnProduct(billID,givinProductID,takingProductID).subscribe(data =>{
      window.alert("Novac koji se vraca je: " + data.moneyToReturn + " RSD")
    },(err) => {this.handleError(err)/*console.log(err.error.message);console.log(err.status);*/});
    
  }
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }
  addToCart(event){

    const id = event.srcElement.id;

    if(this.korpa == null || this.korpa.length == 0){
      this.korpa = [];
      this.korpa.push(id);
    }
    else{

      if(this.korpa.includes(id.toString())){
        alert('Proizvod je vec ubacen u korpu')
      }
      else{
        this.korpa.push(id)
      }

    }


    localStorage.setItem("cart", JSON.stringify(this.korpa));

  }


}
