import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class InvoicesService {

  constructor(private http: HttpClient) { }
  endpointURL: string = 'http://78.46.117.245:9000/';

  headers = new HttpHeaders({'Content-Type' : 'application/json',
                             'token' : localStorage.getItem("token")
                             }); 

  invoices: any;
  response:any;       

 getAllInvoices() {
   return this.http
     .get(this.endpointURL+ 'invoices/',  {headers : this.headers}  )
     .subscribe(result => {
       this.invoices = result;
     });
 }

 blob: Blob;

  getPDF(id){
    var id = id;
    const endPointUrl = this.endpointURL + 'invoices/' + id + '/pdf' ;
    return this.http
    .get(endPointUrl,  {headers : this.headers, responseType: 'blob' as 'json'}  )
    .subscribe((data:any) => {
      this.blob = new Blob([data], {type: 'application/pdf'});
      var downloadUrl = window.URL.createObjectURL(data);
      let filename = "Invoice_" + id;
      var link = document.createElement('a');
      link.href = downloadUrl;
      link.download = filename + ".pdf";
      link.click(); 
      })
    }

}
