import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Product } from '../models/products';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {

  constructor(private http: HttpClient,
    //private home: HomeComponent
    ) { }

endpointURL: string = 'http://78.46.117.245:9000/products/';

headers = new HttpHeaders({'Content-Type' : 'application/json',
                    'token' : localStorage.getItem("token"),
                    }); 

products: Product[];

      getAllProducts() {
      return this.http
      .get<Product[]>(this.endpointURL,  {headers : this.headers}  )
      .subscribe(result => {
      this.products = result;
      });
      }

      discountProduct(producerID,productType,discountCoefficient){
        return this.http.post(this.endpointURL + 'discount/', 
        {
          'producerID' : producerID,
          'productType' : productType,
          'discountCoefficient' : discountCoefficient
        } 
        ,{
          headers: this.headers
        })
      }

      discountSingleProduct(id, discountCoefficient){
        return this.http.post(this.endpointURL + id + '/discount/', 
        {
          'discountCoefficient' : discountCoefficient
        } 
        ,{
          headers: this.headers
        })
      }


}
