import { Component, OnInit } from '@angular/core';
import { SearchService } from '../services/search.service';
import { ProductTypeService } from '../services/product-type.service';
import { UserService } from '../services/user.service';
import { ProductsService } from '../services/products.service';
import { ProducersService } from '../services/producers.service';
import { RegisterService } from '../services/register.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-pretraga',
  templateUrl: './pretraga.component.html',
  styleUrls: ['./pretraga.component.css']
})
export class PretragaComponent implements OnInit {


  constructor(private userService: UserService,
              private registerService: RegisterService,
              private productService: ProductsService,
              private producersService: ProducersService,
              private searchService: SearchService,
              private productTypeService: ProductTypeService,
              private http: HttpClient,
              private router: Router) { }
  pozicija : string = localStorage.getItem('username');
  rola : string = localStorage.getItem("role")

  endpointURL5 : string = 'http://78.46.117.245:9000/logout'
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8',
                             'token' : localStorage.getItem("token")});
  
  routerVar : string;
  statsVar : boolean;
  helpText = "";

  ngOnInit() {

    if(this.rola == 'ADMIN' || this.rola == 'OWNER'){
      this.routerVar = '/admin'; 
      this.statsVar = true;
      this.helpText=`

Poetna

* Na poetnoj strani su izlistani svi proizvodi koji su trenutno dostupni u prodavnici. Mogue je promeniti bilo koji atribut postojaeg proizvoda, mogue je ukloniti proizvod, a takoe i izvriti popust unoenjem odgovarajueg iznosa procenta. Na crvenu strelicu se proizvod ubacuje u korpu. 


Spisak radnika

* Omoguen je uvid u spisak svih radnika koji rade u magacinu Techtradea kao i njihove informacije. Mogue je ukloniti korisnika.


Naruivanje proizvoda

* Na poetku su izlistani svi proizvodi svih dobavljaa. Prilikom narudbenice, mogue je u korpu dodati proizvode pritiskom na crvenu strelicu. U korpi eljeni kvantitet unosi sam korisnik. Mogue je obrisati sadraj iz korpe. Pritiskom na dugme za kupovinu potvruje se porudzbina. 


Narueni proizvodi

* Omoguen je uvid u dve tabele, Narueni proizvodi koji treba da stignu i Narueni proizvodi koji su stigli.


Zameni proizvod

* Izvriti odabir ifre rauna;
* Izvriti odabir ifre proizvoda na raunu;
* Uneti ifru eljenog proizvoda za zamenu;
* Pritiskom na dugme 'Zameni' izbacie se odgovarajua poruka.


Registracija radnika

* Omoguava unoenje novih autorizovanih korisnika. Unoenjem svih potrebnih podataka, kao i podatka koji autorizovani korisnik je u pitanju, bie izbaena odgovarajua poruka.


Dodavanje proizvoda

* Mogue je dodati nov proizvod. U sluaju da proizvod ve postoji, bie izbaena odgovarajua poruka.


Odredi popust

* Odabrati proizvoaa;
* Odabrati eljeni proizvod;
* Uneti iznos procenta;
* Pritiskom na dugme 'Izvri', bie izbaena odgovarajua poruka.


Automatski popust

* Omoguen je uvid u spisak svih proizvoda kojima je omoguen automatski popust i potvrda popusta.


Prikaz rauna

* Omoguen je uvid u tabelu sa raunima;
* Mogue je izvesti raun koji je generisan u PDF formi.


Prikaz fakture

* Omoguen je uvid u tabelu faktura;
* Mogue je izvesti fakturu u PDF formi.
`
    }
    else if (this.rola == 'MANAGER'){
      this.routerVar = '/komercijalista';
      this.helpText=`


Poetna

* Na poetnoj strani su izlistani svi proizvodi svih dobavljaa. Mogue je izvriti izmenu cene eljenog proizvoda.


Naruivanje proizvoda

* Kada radnik narui proizvod, komercijalista e to videti i ti proizvodi e se pojaviti u tabeli. U narudzbenici svi proizvodi moraju biti od istog dobavljaa. Komercijalista bira proizvode koje treba da narui od dobavljaa i njihov kvantitet. 


Narueni proizvodi

* Omoguen je uvid u dve tabele, Narueni proizvodi koji treba da stignu i Narueni proizvodi koji su stigli.


Prikaz fakture

* Omoguen je uvid u tabelu faktura;
* Mogue je izvesti fakturu u PDF formi.


Automatski popust

* Omoguen je uvid u spisak svih proizvoda kojima je omoguen automatski popust i potvrda popusta.

`
    }
    else if (this.rola == 'WORKER'){
      this.routerVar = '/radnik';
      this.helpText="* Radnik ima mogunost da prodaje proizvod, vri zamenu proizvoda kao i da narui proizvode kojih nema na stanju. Kada radnik izabere proizvod koji hoe da proda, klikom na crvenu strelicu dodaje taj proizvod u korpu. Preicom u gornjem desnom uglu se pristupa korpi. Nakon odabira koliine proizvoda kojeg korisnik eli da porui, pritiskom na dugme Izvri kupivinu potvruje se porudzbina. Mogue je obrisati sadraj iz korpe. Radnik naruuje proizvode kojih nema na stanju i svi proizvodi se alju komercijalisti i on ih obrauje.Zamena proizvoda se izvrava nakon odabira ifre rauna, ifre proizvoda na raunu, a zatim i ifre eljenog proizvoda za zamenu. Pritiskom na dugme 'Zameni' izbacie se odgovarajua poruka.";
    }



  }
  korpa = JSON.parse(localStorage.getItem("cart"));

  logout(){
    this.http.post(this.endpointURL5,{"logout": "logout"}, {headers:this.headers}).subscribe(status => {

      if(status){
        this.router.navigate([""]);
        localStorage.removeItem("token");
      }
    })
  }
 

  addToCart(event){
    const id = event.srcElement.id;

    if(this.korpa == null || this.korpa.length == 0){
      this.korpa = [];
      this.korpa.push(id);
    }
    else{

      if(this.korpa.includes(id.toString())){
        alert('Proizvod je vec ubacen u korpu')
      }
      else{
        this.korpa.push(id)
      }

    }


    localStorage.setItem("cart", JSON.stringify(this.korpa));
    
  }
  
  handleError(error) {
    if(error.status != 200){

        alert(error.error.message);

    }
  }

}
